import api from '~api'
import config from '~api-config'

let listSp = () => api.get('/product/productSpec/list.action',config.apiSpxx);//
let createSp = (params) => api.post('/product/productSpec/create.action',config.apiSpxx,params);//
let editSp = (params) => api.post('/product/productSpec/edit.action',config.apiSpxx,params);//
let updateSp = (params) => api.post('/product/productSpec/update.action',config.apiSpxx,params);//
let deleteSp = (params) => api.post('/product/productSpec/delete.action',config.apiSpxx,params);//
//let bmSp = (params) => api.post('/base/company/type/list.action',config.api,params);//
let flSp = (params) => api.post('/product/productCategory/list.action',config.apiSpxx,params);//
let createFl = (params) => api.post('/product/productCategory/create.action',config.apiSpxx,params);//
let deleteFl = (params) => api.post('/product/productCategory/delete.action',config.apiSpxx,params);//
let updateFl = (params) => api.post('/product/productCategory/update.action',config.apiSpxx,params);//

export default {
    listSp,
    createSp,
    editSp,
    updateSp,
    deleteSp,
    //bmSp
    flSp,
    createFl,
    deleteFl,
    updateFl
}



